@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;800;900&display=swap);
html {
    overflow-x: hidden;
}

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  background-color: white;
  color: black;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 24px;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
    text-decoration: none;
    margin-left: 3.6px;
    margin-right: 3.6px;
}

img {
    max-width: 100%;
    overflow: auto;
    border-radius: 6px;
}

h4 {
    font-weight: 700;
    text-shadow: 0 1px 0 black;
}

button:focus{
    outline:0px;
}

.header-bar {
    display: flex;
    padding: 0px 24px;
    border-bottom: 2px solid rgba(0,0,0,.07);
    background: white;
}

.links {
    position: absolute;
    right: 0;
    margin-right: 10px;
}

@media screen and (max-width: 460px) {
    .links {
        position: relative;
    }
}

.hero-img-wrapper{
    display:flex;
}
.img-underlay {
    background-color: #feefec;
    border-radius: 6px;
    padding: 24px;
    margin-bottom: 24px;
}

.img-underlay.hero {
    max-height: 300px;
}

.img-wrapper {
    right: 0;
    z-index: 1;
    max-width: 403px;
    max-height: 215px;
    display: flex;
    box-shadow: 0 1px 5px rgba(0,0,0,.05), 0 5px 30px rgba(0,0,0,.2);
    border-radius: 6px;
}

.container {
    display: flex;
    position: relative;
    flex-wrap: wrap;
    background-color: white;
    box-shadow: 0 1px 5px rgba(0,0,0,.05), 0 5px 30px rgba(0,0,0,.2);
    border-radius: 6px;
    padding: 24px;
    max-width: 100%;
    margin-bottom: 24px;
}

.about-container {
    display: flex;
    position: relative;
    flex-wrap: wrap;
    max-width: 100%;
    margin-bottom: 50px;
    margin-top: 100px;
    align-items: center;
    padding: 0px 50px;
}

@media screen and (min-width: 100px) and (max-width: 450px){
    .about-container {
        margin-top: 150px;
        padding: 0px;
    }
    .img-underlay.hero {
        max-height: 140px;
    }
    .img-wrapper {
        max-height: 150px;
    }
}

@media screen and (min-width: 650px) {
    .img-underlay.hero {
        max-height: 250px;
    }
    .img-wrapper {
        max-height: 250px;
    }
}

@media screen and (min-width: 450px) {
    .img-underlay.hero {
        max-height: 200px;
    }
    .img-wrapper {
        max-height: 200px;
    }
}

@media screen and (max-width: 785px) {
    .about-container{
        padding:0px;
    }
}

@media screen and (max-width: 1160px) {
    .about-container {
        flex-direction: column;
    }
}

.resume-container {
    position: relative;
}

.text-wrapper {
    left: 0;
    flex: 1 1;
    margin-right: 24px;
}

.button-wrapper {
    margin: 48px 0px;
}

@media screen and (max-width: 1020px) {
    .text-wrapper {
        flex: 100% 1;
        position: relative;
        margin: 0px 0px 24px 0px;
        max-width: 100%;
        flex-direction: column;
    }
    .img-wrapper {
        flex: 100% 1;
        flex-direction: column;
    }
    .img-size{
        width: 100%;
    }
}

.icon-container {
    padding: 24px 128px;
    position: relative;
    display: flex;
    justify-content: space-between;
    width: 500px;
}

.center-container {
    width: 100%;
    display: flex;
    justify-content: center;
}

.project-container {
    display: flex;
    position: relative;
    flex-wrap: wrap;
}

.container.left {
    flex:1 1;
    left:0;
    margin-right: 24px;
    display: block;
}

@media screen and (max-width: 1060px) {
    .container.left {
        margin-right: 0px;
        flex-direction: column;
        flex: 100% 1;
        margin-bottom: 24px;
    }
    .project-img-wrapper {
        width: 100%;
        justify-content: center;
    }
}

.project-container.center {
    align-items:center;
}

.project-img-wrapper {
    display: flex;
    align-items: center;
    margin-bottom: 24px;
}

.img-underlay.c4me {
    height: 200px;
}

.img-underlay.tab {
    height: 250px;
}

.img-overlay.tab {
    max-width: 200px;
    height: 250px;
}

.img-overlay {
    max-width: 450px;
    height: 200px;
    right: 0;
    z-index: 1;
    display: flex;
    box-shadow: 0 1px 5px rgba(0,0,0,.05), 0 5px 30px rgba(0,0,0,.2);
    border-radius: 6px;
}

.tabguard-size {
    width: 200px;
    height: 250px;
}

.predictor-size {
    width: 450px;
    height: 200px;
}

.c4me-size {
    width: 450px;
    height: 200px;
}

iframe.border {
    border-radius: 6px;
    overflow: hidden;
    min-width: 200px;
    min-height: 280px;
}

@media screen and (max-width: 450px) {
    iframe {
        max-width: 100%;
    }
}

.interests-container {
    position: relative;
    display: flex;
    margin-bottom: 24px;
    flex-wrap: wrap;
}

.interests-wrapper {
    box-shadow: 0 1px 5px rgba(0,0,0,.05), 0 5px 30px rgba(0,0,0,.2);
    border-radius: 6px;
    padding: 24px;
    flex: 22% 1;
}

.interests-wrapper.right {
    margin-right: 24px;
}

.interests-wrapper.right2 {
    margin-right: 24px;
}

.carousel-size {
    max-width: 300px;
    max-height: 400px;
}

.media-wrapper {
    display:flex;
    justify-content: center;
}

@media screen and (max-width: 1370px) {
    .interests-wrapper {
        flex: 45% 1;
        margin-bottom: 24px;
    }
    .interests-wrapper.right2 {
        margin-right: 0px;
    }
}

@media screen and (max-width: 785px) {
    .interests-wrapper {
        flex-direction: column;
        flex: 100% 1;
        margin-bottom: 24px;
    }
    .interests-wrapper.right {
        margin-right: 0px;
    }
    .interests-wrapper.right2 {
        margin-right: 0px;
    }
}

